'use client';

import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Cloud, ServerCrash, CheckCircle2, RefreshCw } from 'lucide-react';
import { useDAWStore } from '@/store/useDAWStore';

export function CloudStatusPanel() {
    const cloudStatus = useDAWStore((state) => state.cloudStatus);
    const systemMessage = useDAWStore((state) => state.systemMessage);
    const setCloudStatus = useDAWStore((state) => state.setCloudStatus);

    const [isHovered, setIsHovered] = useState(false);
    const [showNotification, setShowNotification] = useState(false);

    // Simulate connection or make real request
    const handleDeploy = async () => {
        if (cloudStatus === 'connecting' || cloudStatus === 'connected') return;

        setCloudStatus('connecting', 'Conectando con el cerebro en la nube...');

        try {
            // Trigger local API route that communicates with Modal
            const formData = new FormData();
            formData.append('type', 'audio');

            const res = await fetch('/api/train', {
                method: 'POST',
                body: formData
            });

            if (res.ok) {
                setCloudStatus('connected', 'Motor V1 Activo. Listo para procesar tu hit, Danny.');
                setShowNotification(true);
                // Hide notification after 5 seconds
                setTimeout(() => setShowNotification(false), 5000);
            } else {
                setCloudStatus('disconnected', 'Fallo en la conexión. El motor no se pudo desplegar.');
            }
        } catch (error) {
            console.error(error);
            setCloudStatus('disconnected', 'Error de red conectando con el Motor IA.');
        }
    };

    return (
        <div className="absolute top-6 right-6 z-50 flex flex-col items-end gap-2 pointer-events-auto">
            {/* Main Status Panel */}
            <motion.div
                initial={{ opacity: 0, scale: 0.9, y: -20 }}
                animate={{ opacity: 1, scale: 1, y: 0 }}
                transition={{ duration: 0.5, type: 'spring', bounce: 0.4 }}
                className="bg-[#111113]/80 backdrop-blur-xl border border-[#333] shadow-[0_10px_30px_rgba(0,0,0,0.5)] rounded-lg p-3 flex flex-col gap-3 min-w-[240px]"
                onMouseEnter={() => setIsHovered(true)}
                onMouseLeave={() => setIsHovered(false)}
            >
                <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                        {/* LED Indicator */}
                        <div className={`w-2.5 h-2.5 rounded-full shadow-[0_0_8px_currentColor] transition-colors duration-300 ${cloudStatus === 'connected' ? 'bg-[#00F0FF] text-[#00F0FF]' :
                            cloudStatus === 'connecting' ? 'bg-[#FF6B00] text-[#FF6B00] animate-pulse' :
                                'bg-[#555] text-transparent'
                            }`} />
                        <span className="text-xs font-bold tracking-widest text-[#E0E0E0]">
                            MODAL ENGINE
                        </span>
                    </div>
                </div>

                <div className="flex flex-col gap-2">
                    {cloudStatus !== 'connected' && (
                        <button
                            onClick={handleDeploy}
                            disabled={cloudStatus === 'connecting'}
                            className={`flex items-center justify-center gap-2 w-full py-1.5 rounded transition-all duration-300 text-xs font-bold tracking-widest border ${cloudStatus === 'connecting'
                                ? 'bg-[#FF6B00]/20 border-[#FF6B00]/50 text-[#FF6B00]'
                                : 'bg-[#222] hover:bg-[#333] border-[#444] text-white hover:border-[#FF6B00]/50 hover:shadow-[0_0_10px_rgba(255,107,0,0.2)]'
                                }`}
                        >
                            {cloudStatus === 'connecting' ? (
                                <>
                                    <RefreshCw size={12} className="animate-spin" />
                                    CONECTANDO...
                                </>
                            ) : cloudStatus === 'disconnected' && systemMessage.includes('Fallo') ? (
                                <>
                                    <ServerCrash size={12} className="text-red-400" />
                                    REINTENTAR
                                </>
                            ) : (
                                <>
                                    <Cloud size={12} />
                                    DESPLEGAR MOTOR IA
                                </>
                            )}
                        </button>
                    )}

                    {/* Compact System Message viewable on hover or when connecting */}
                    <AnimatePresence>
                        {(isHovered || cloudStatus === 'connecting' || (cloudStatus === 'disconnected' && systemMessage)) && (
                            <motion.p
                                initial={{ opacity: 0, height: 0 }}
                                animate={{ opacity: 1, height: 'auto' }}
                                exit={{ opacity: 0, height: 0 }}
                                className="text-[10px] text-[#A0A0A0] leading-tight"
                            >
                                {systemMessage || 'Conexión inactiva. El motor está en reposo.'}
                            </motion.p>
                        )}
                    </AnimatePresence>
                </div>
            </motion.div>

            {/* Success Notification Pop-up */}
            <AnimatePresence>
                {showNotification && (
                    <motion.div
                        initial={{ opacity: 0, x: 50, scale: 0.9 }}
                        animate={{ opacity: 1, x: 0, scale: 1 }}
                        exit={{ opacity: 0, x: 50, scale: 0.9 }}
                        className="bg-[#0A0A0C]/90 backdrop-blur-md border outline outline-1 outline-[#00F0FF]/30 border-[#00F0FF]/50 shadow-[0_0_20px_rgba(0,240,255,0.2)] rounded-lg p-4 flex items-start gap-4 max-w-[320px]"
                    >
                        <div className="w-8 h-8 rounded-full bg-gradient-to-br from-[#FF6B00] to-[#FF9A4A] flex items-center justify-center shadow-[0_0_10px_rgba(255,107,0,0.5)] shrink-0">
                            {/* Micro DA GRABA Logo */}
                            <span className="text-[8px] font-black tracking-tighter text-black">DG</span>
                        </div>
                        <div className="flex flex-col">
                            <h4 className="text-sm font-bold text-white flex items-center gap-1">
                                <CheckCircle2 size={14} className="text-[#00F0FF]" />
                                Motor Listo
                            </h4>
                            <p className="text-xs text-[#AAA] mt-1">
                                {systemMessage}
                            </p>
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
}
